import { RefObject, useEffect, useRef } from 'react';

import styles from './styles.scss';

/** Переопределить стиль открытого компонента */
export default function useStyles(isOpenDropdown: boolean): RefObject<HTMLInputElement> {
  const baseInputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    if (baseInputRef.current) {
      if (isOpenDropdown) {
        baseInputRef.current.classList.add(styles.open_container);
      } else {
        baseInputRef.current.classList.remove(styles.open_container);
      }
    }
  }, [isOpenDropdown]);

  return baseInputRef;
}
